<template>
  <div class="register">
    <h2></h2>
    <div class="input">
      
      <input
        type="username"
        id="phone"
        class="inputbox"
        v-model="username"
        placeholder="/"
      />
      <br />
      <br />
      <input type="password" id="upwd" class="crypto" v-model="password" placeholder="" />
    </div>
    <div class="chose">
      <input type="checkbox" value="0" />
      <a href="javascript:;" class="password"></a>
    </div>
    <button class="btn" @click="login"></button>
    <!-- <div>
              <sessionStorage class="setitem" name=""></sessionStorage>
    </div>-->
  </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  data() {
    return {
      username: "",
      password: "",
      loading: false
    };
  },
  methods: {
    ...mapActions(["setUser"]),
    login() {
      var url = "use/";
      var obj = {
        user_name: this.username,
        user_pwd: this.password
      };
      this.axios.get(url, { params: obj }).then(result => {
        //console.log(result);
        if (result.data.code != 1) {
          this.$message({
            showClose: true,
            message: "",
            type: "err",
            duration:1000
          });
        } else {
            this.$message({
            showClose: true,
            message: result.data.msg,
            type: "success",
            duration:1000
          });
          this.setUser(result.data.data[0].user_name);
          //
          //this.$router.go(-2);
          var user_id = result.data.data[0].user_id;
          //session
          sessionStorage.setItem("user_id", user_id);
          //session
          console.log(sessionStorage.getItem("user_id"));
          this.$router.push("/index");
        }
      });

      // var uname=document.getElementById("uname");
      // sessionStorage.setItem('uname',uname.value);
      // alert("2")
      // setTimeout(function(){
      //     location.href='index.vue'
      // },2000);
    }
  },
  created() {}
};
</script>
