  margin-left: -1px;
  margin-top: 43px;
}
.chose > a {
  margin-left: 136px;
}
.btn {
  padding: 5px;
  width: 332px;
  height: 43px;
  margin-top: 17px;
  background: #ff5a5f;
  border-color: #ff5a5f;
  margin-right: 38px;
  border-radius: 5px;
}
</style>