      if (i == 1) {
        if (this.peoplenum1 > 1) {
          this.peoplenum1--;
        }
      }
      if (i == 2) {
        if (this.peoplenum2 > 0) {
          this.peoplenum2--;
        }
      }
      if (i == 3) {
        if (this.peoplenum3 > 0) {
          this.peoplenum3--;
        }
      }
