      if (i == 1) {
        if (
          this.peoplenum1 <
          this.houseData[0].House_people_num - this.peoplenum2
        ) {
          this.peoplenum1++;
        }
      }
      if (i == 2) {
        if (
          this.peoplenum2 <
          this.houseData[0].House_people_num - this.peoplenum1
        ) {
          this.peoplenum2++;
        }
      }
      if (i == 3) {
        if (this.peoplenum3 < 5) {
          this.peoplenum3++;
        }
      }
