                center: [116.412978, 39.895807],
                viewMode: '3D' //3D
            });*/
      //
      var map = new BMap.Map("map");
      // ,
      map.centerAndZoom(new BMap.Point(116.419378, 39.901908), 19);
      //
      map.addControl(
        new BMap.MapTypeControl({
          mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
        })
      );
      //  
      map.setCurrentCity("");
      //
      map.enableScrollWheelZoom(true);
      var point = new BMap.Point(116.419378, 39.901908); //
      map.centerAndZoom(point, 14); //
      var html = `<img @click="moremap"  src="https://z1.muscache.cn/ms/static/packages/home_pin.fb50ae56.png" class="pointimg">`;
      //var marker = new BMap.Marker(point); //
      //map.addOverlay(marker); //
      var myRichMarkerObject = new BMapLib.RichMarker(html, point, {
        anchor: new BMap.Size(0, 0),
        enableDragging: false
      });
      map.addOverlay(myRichMarkerObject);
    }
