<script>
  export default {
    data() {
      return {
        list: [
          { urly: "userdata", msg: "" },
          { urly: "second", msg: "" },
          { urly: "", msg: "" },
          { urly: "", msg: "" },
          { urly: "", msg: "" }
        ],
        name: "",
        email: "",
        phone: "",
        sex: "",
        user_login_time: "",
        user_reg_time: "",
        user_imgurl: "",
        base64: ""
      };
    },
    created() {
      this.getuser();
      /*
      //post
      //stringifyjs
      var url = http://127.0.0.1:3000/
      var postData = this.qs.stringify({
        :,
        :
      })
      this.axios.post(url,postData).then(result=>{
        console.log(result)
      })
      */
    },
    methods: {
      getuser() {
        this.axios.get("user/").then(result => {
          if (result.data.code > 0) {
            this.name = result.data.data[0].user_name;
            this.email = result.data.data[0].user_email;
            this.phone = result.data.data[0].user_phone;
            this.sex = result.data.data[0].user_gender;
            this.user_login_time = result.data.data[0].user_login_time;
            this.user_reg_time = result.data.data[0].user_reg_time;
            if (result.data.data[0].user_imgurl == null) {
              this.user_imgurl = `https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png`;
            } else {
              this.user_imgurl =
                `http://127.0.0.1:3000/` + result.data.data[0].user_imgurl;
            }
          } else {
            this.$alert(",!", "");
          }
        });
      },
      open() {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "!"
            });
            this.axios
              .get("user/user_update", {
                params: {
                  name: this.name,
                  email: this.email,
                  phone: this.phone,
                  sex: this.sex
                }
              })
              .then(result => {
                console.log(result);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      },
      chooseImg(event) {
        var file = event.target.files[0];
        var reader = new FileReader();
        var img = new Image();
        // 
        reader.readAsDataURL(file);
        // 
        reader.onloadend = e => {
          img.src = e.target.result;
          // e.targetreader
          // console.log(reader.result)
          // reader.resultbase64
          this.base64 = reader.result;
        };
      },
      open1() {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    }
  };
</script>
