<template>
<div class='footer'>
    <div id='btn'>
        <h2></h2>
        <!-- <h3>?
      <a class="password"></a> -->
    <!-- </h3> -->
    </div>
    <div class='zc'>
        <input type="text" @blur="selName"  id='uname' v-model='name' placeholder='' >
        <br>
        <br>
        <input type="password" id='upwd'  v-model='password' placeholder="">
        <br>
        <br>
        <div class="gender_radio">
        
                <el-radio v-model="radio" label="1"></el-radio>
                <el-radio v-model="radio" label="0"></el-radio>
        </div>        
             <!-- <label>   
            <input type="radio" id='gender' v-model='age' name="gender"></label>
            <label>
            <input type="radio" id='gender' v-model='age' name="gender"></label>
            <span id="u_gender"></span> -->
        <br>
        <input type="text" id='phone' v-model='call' placeholder=''>
        <br>
        <br>
        <input type="text" id='email' v-model='email' placeholder=''>
        <br> 
        <br>
    </div>
        <div class='account'>
            <a :href="`#/`+'loginzmw'" ></a>
        </div>
    <div class='sign'>
        <el-button :plain="true" class='btn1' @click='regin'></el-button>
    </div>
</div>
<!-- <div id='msg'></div>
<a href="loginzme.vue"></a> -->
</template>
<script>
 export default {
     data(){
        return{ 
            name:'',
            password:'',
            age:'',
            call:'',
            email:'',
            radio:'1'
        }
    },
    methods:{
        regin(){
            //console.log(this.name,this.password);
            if(this.name==""){
                this.$message("");
                return;
            }
            if(!this.password){
                this.$message("");
                return;
            }
            if(!this.radio){
                this.$message("");
            }
            if(!this.call){
                this.$message("");
            }
            if(!this.email){
                this.$message("");
            }
            var url="/use/reg";
            var obj={
                user_name:this.name,
                user_pwd:this.password,
                user_gender:this.radio,
                user_phone:this.call,
                user_email:this.email
            };
            this.axios.get(url,{params:obj}).then(result=>{
                //console.log(result.data.data);
                if(result.data.data==""){
                    this.$message("");
                }
            });
            
        },
        selName(){
            var url="/use/selName";
            var obj={user_name:this.name};
            this.axios.get(url,{params:obj}).then(result=>{
                //console.log(result.data);
                if(!this.name){
                    this.$message("");
                }else{
                    this.$message(result.data.data);
                    
                }
                
            });
        }
    },
    created(){

    }
}
</script>
